.pika-mailman-carry {
    --animation-scale: 3.5;
    --origin-position: -0.3vw -18.5vh;
    --black: rgb(0, 0, 0);
    --green-grass: rgb(93, 93, 85);
    --green-grass-accent: rgb(76, 175, 80);
    --yellow-skin: rgb(227, 215, 16);
    --yellow-skin-accent: rgb(232, 224, 102);

    position: relative;
    transform: scale(var(--animation-scale));
    transform-origin: var(--origin-position);
    animation: carry 5s forwards;
    -webkit-animation: carry 5s forwards;
    -moz-animation: carry 5s forwards;
    -o-animation: carry 5s forwards;
}

@media (min-width: 1201px) {
    .pika-mailman-carry {
        --animation-scale: 6;
        --origin-position: -0.3rem 0rem;
    }
}
@media (max-width: 1200px) and (min-width: 1026px) {
    .pika-mailman-carry {
        --animation-scale: 5;
        --origin-position: -0.3rem -1rem;
    }
}
@media (max-width: 1025px) and (min-width: 901px) {
    .pika-mailman-carry {
        --animation-scale: 4.5;
        --origin-position: -0.3rem -1rem;
    }
}
@media (max-width: 900px) and (min-width: 769px) {
    .pika-mailman-carry {
        --animation-scale: 3.8;
        --origin-position: -0.3rem -3rem;
    }
}
@media (max-width: 768px) and (min-width: 699px) {
    .pika-mailman-carry {
        --animation-scale: 3.2;
        --origin-position: -0.3rem -4rem;
    }
}
@media (max-width: 698px) and (min-width: 601px) {
    .pika-mailman-carry {
        --animation-scale: 2.8;
        --origin-position: -0.3rem -5rem;
    }
}
@media (max-width: 600px) and (min-width: 551px) {
    .pika-mailman-carry {
        --animation-scale: 2.5;
        --origin-position: -5rem 1rem;
    }
}

@media (max-width: 550px) {
    .pika-mailman-carry {
        --animation-scale: 2;
        --origin-position: -6rem 1rem;
    }
}

